.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.switch-icons {
  background-color: white;
  border: 2px solid black;
  border-radius: 5px;
  padding: 15px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
  color: #000000;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: gray;
}

.anticon svg {
  font-size: 25px !important;
  color: rgb(0, 0, 0);
}

.ant-menu .ant-menu-submenu-title .ant-menu-item-icon svg {
  background-color: rgb(255, 255, 255);
  border-radius: 50px;
}

.ant-menu .ant-menu-item .ant-menu-item-icon svg {
  color: rgb(255, 255, 255);
}

#headernavbar li:first-child {
  float: left;
  position: absolute;
  color: white !important;
}

#deleteBtn:hover {
  color: red;
}

#editBtn:hover {
  color: rgb(117, 117, 250);
}

#transactionTable {
  width: 85%;
  margin: 18px auto;
}

.ant-pagination .ant-pagination-item-active a {
  color: #000000 !important;
}

.ant-pagination .ant-pagination-item-active {
  border-color: #000000;
}

.ant-pagination .ant-pagination-item-active:hover {
  border-color: #000000;
}

.ant-table-wrapper .ant-table-tbody > tr > td {
  background-color: #b7b7b778;
}

.ant-table-wrapper .ant-table-thead > tr > th {
  color: white;
  background-color: black;
}

.ant-table-container table > thead > tr:first-child > *:first-child {
  background-color: #000000;
}

#deleteBtn {
  color: #000000 !important;
}

.ant-select-single.ant-select-show-arrow .ant-select-selection-item {
  padding-right: 40px;
}

.ant-select:not(.ant-select-customize-input) .ant-select-selector {
  border: 2px solid black;
}

h6 {
  margin-bottom: 10px;
}

.ant-menu .ant-menu-submenu-title {
  color: white !important;
}

.ant-menu-item-selected {
  text-decoration: none !important;
  color: inherit !important;
}

.category-left,
.category-right {
  font-size: large;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 20px;

  border-radius: 40px;

  box-shadow: 0px 0px 8px gray;
}

.card {
  align-items: center;
  text-align: center;
}

.card-body {
  display: flex;
  align-items: center;
  text-align: center;
  padding: 10px;
}

.card-body-left,
.card-body-right {
  flex-basis: 100%;
  font-size: large;
}

.circle {
  padding-top: 30px;
}

.card-sub-header {
  font-size: large;
}

.success {
  color: #4bb543;
}

.danger {
  color: red;
}

.card-header {
  font-size: x-large;
  font-weight: bolder;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 5px;
  margin-bottom: 50px;
}

.card-sub-body {
  display: flex;
}

.card-sub-body-header {
  font-size: large;
  padding-bottom: 10px;
}

.card-body2 {
  display: flex;
}

.card-body2-left,
.card-body2-right {
  flex-basis: 100%;
  font-size: large;
  padding-left: 10px;
}

/* CSS rule to prevent horizontal scrolling */
body {
  overflow-x: hidden !important;
}

.responsive-table-container {
  overflow-x: auto;
  margin-bottom: 20px;
}

@media screen and (max-width: 576px) {
  .responsive-table-container .ant-table {
    overflow-x: auto;
    display: block;
  }

  .responsive-table-container .ant-table table {
    width: 100%;
    white-space: nowrap;
  }

  .responsive-table-container .ant-table-thead > tr > th,
  .responsive-table-container .ant-table-tbody > tr > td {
    display: inline-block;
    white-space: normal;
    text-align: center;
  }

  th.ant-table-cell {
    width: 100px;
    height: 50px;
  }
}

.ant-table-wrapper .ant-table {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: rgb(0 0 0 / 88%);
  font-size: 14px;
  line-height: 1.5714285714285714;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  border-radius: 8px 8px 0 0;
  background-color: white;
}

@media screen and (max-width: 576px) {
  .responsive-table-container .ant-table-thead > tr > th,
  .responsive-table-container .ant-table-tbody > tr > td {
    display: inline-block;
    white-space: normal;
    text-align: center;
  }
  .ant-table-wrapper .ant-table-tbody > tr > td {
    background-color: #b7b7b778;
  }
  .ant-table-wrapper .ant-table-tbody > tr > td {
    transition: background 0.2s, border-color 0.2s;
    border-bottom: 1px solid #f0f0f0;
  }
}

