body {
    /* background-image: linear-gradient(135deg, #fab2ff 10%, #1904e5 100%); */
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #333;
}
.box-form {
    margin: 2rem auto;
    width: 35%;
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    flex: 1 1 100%;
    align-items: stretch;
    justify-content: space-between;
    box-shadow: 0 0 20px 6px #090b6f 85;
}
/* @media (max-width: 980px) {
    .box-form {
        flex-flow: wrap;
        text-align: center;
        align-content: center;
        align-items: center;
   }
} */
.box-form div {
    height: auto;
}
.box-form .left {
    color: #fff;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url("https://life.futuregenerali.in/media/fyvhebzt/how-to-save-money-from-your-monthly-salary.webp");
    overflow: hidden;
}
.box-form .left .overlay {
    padding: 30px;
    width: 100%;
    height: 100%;
    background: #5961f9 ad;
    overflow: hidden;
    box-sizing: border-box;
}
.box-form .left .overlay h1 {
    font-size: 10vmax;
    line-height: 1;
    font-weight: 900;
    margin-top: 40px;
    margin-bottom: 20px;
}
.box-form .left .overlay span p {
    margin-top: 30px;
    font-weight: 900;
}
.box-form .left .overlay span a {
    background: #3b5998;
    color: #fff;
    margin-top: 10px;
    padding: 14px 50px;
    border-radius: 100px;
    display: inline-block;
    box-shadow: 0 3px 6px 1px #042d46 57;
}
.box-form .left .overlay span a:last-child {
    background: #1dcaff;
    margin-left: 30px;
}
.box-form .right {
    padding: 30px;
    overflow: hidden;
}
@media (max-width: 980px) {
    .box-form .right {
        width: 100%;
   }
   .box-form{
    width: 98%;
   }
   .box-form .right input{
    width: 86% !important;
   }
   .box-form .right button{
    margin-right: 50px !important;
   }
}
.box-form .right h5 {
    font-size: 35px;
    text-align: center;
    margin: 10px auto;
    line-height: 0;
}
.box-form .right p {
    font-size: 14px;
    color: #b0b3b9;
    margin-top: 30px;
}
.box-form .right .inputs {
    overflow: hidden;
}
.box-form .right input {
    width: 100%;
    padding: 10px;
    margin-top: 0px;
    font-size: 16px;
    border: none;
    outline: none;
    border: 2px solid #b0b3b9;
}
.box-form .right .remember-me--forget-password {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.box-form .right .remember-me--forget-password input {
    margin: 0;
    margin-right: 7px;
    width: auto;
}
.box-form .right button {
    float: right;
    color: #fff;
    font-size: 16px;
    padding: 12px 35px;
    border-radius: 50px;
    display: inline-block;
    border: 0;
    outline: 0;
    background-image: #ffc600;
}
.box-form .right button:hover{
    cursor: pointer;
    background-image: linear-gradient(135deg, #b2b5ff 10%, #1904e5 100%);
    transition: all 1s;
}
label {
    display: block;
    position: relative;
    margin-left: 30px;
}
label::before {
    content: ' \f00c';
    position: absolute;
    font-family: FontAwesome;
    background: transparent;
    border: 3px solid #70f570;
    border-radius: 4px;
    color: transparent;
    left: -30px;
    transition: all 0.2s linear;
}
label:hover::before {
    font-family: FontAwesome;
    content: ' \f00c';
    color: #fff;
    cursor: pointer;
    background: #70f570;
}
label:hover::before .text-checkbox {
    background: #70f570;
}
label span.text-checkbox {
    display: inline-block;
    height: auto;
    position: relative;
    cursor: pointer;
    transition: all 0.2s linear;
}
label input[type="checkbox"] {
    display: none;
}


.right{
    background-color: black;
    color: white;
}