.grid2-container {
  display: grid;
  grid-template-columns: 1fr; /* Default: One column */
  grid-template-rows: auto auto; /* Two rows with auto height */
  gap: 10px; /* Gap between grid items */

  /* Media query for mobile and tablet sizes */
  @media screen and (min-width: 0px) and (max-width: 767px) {
    grid-template-columns: 1fr 1fr; /* Two columns for mobile and tablet sizes */
    grid-template-rows: auto auto; /* Two rows with auto height */
  }

  /* Media query for larger screens */
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr 1fr 1fr; /* Four columns for larger screens */
    grid-template-rows: auto; /* One row with auto height */
  }

  justify-items: center; /* Center items horizontally */
  align-items: center; /* Center items vertically */
}

.grid2-item {
  /* background-color: #eaeaea; */
  padding: 20px;
  text-align: center;
}
